$clr--dark: #020917;
$clr-footer: #171c20;
$clr-link: #bb1122;
$clr-accent: #006194;
$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: 'Montserrat',
sans-serif;

html {
  height: 100%;
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;

  display: flex;
  flex-direction: column;

  height: 100%;
}

:root {
  --min-width-l: min(100% - 1rem, 105em, 95vw);
  --min-width-m: min(100% - 1rem, 90em, 90vw);
  --min-width-s: min(100% - 1rem, 50em, 90vw);

  --section-gap: 5rem;

  --step--2: clamp(1rem, calc(1rem + 0.10vw), 1.14rem);
  --step--1: clamp(1.09rem, calc(1.05rem + 0.21vw), 1.20rem);
  --step-0: clamp(1.31rem, calc(1.24rem + 0.37vw), 1.50rem);
  --step-1: clamp(1.58rem, calc(1.46rem + 0.59vw), 1.88rem);
  --step-2: clamp(1.89rem, calc(1.71rem + 0.89vw), 2.34rem);
  --step-3: clamp(2.27rem, calc(2.01rem + 1.29vw), 2.93rem);
  --step-4: clamp(2.72rem, calc(2.36rem + 1.83vw), 3.66rem);
  --step-5: clamp(3.27rem, calc(2.75rem + 2.56vw), 4.58rem);
}

section {
  padding-top: 5rem;
  margin-bottom: 4rem;
}


.mobile-only {
  display: none;
}

// Dark Container
.dark-con {
  background-color: $clr--dark;
  color: #ffffff;
}

// General Links
// a:visited,
// a:link {
//   color: #722F37;
//   text-decoration: none;
// }

p > a:link,
p > a:visited {
  text-decoration: underline;
}
a:hover,
a:active,
a:focus {
  color: #722F37;
}

.clr-accent {
  color: $clr-accent
}

// General Button
.btn {
  text-transform: uppercase;
  background-color: #00619465;
  border-radius: 3px;
  font-weight: 700;
  border: none;
  padding: 0.25rem 5rem;
  font-weight: 700;
}
// Links inside btn
.btn a {
  color: #000000;
}

.btn:focus,
.btn:hover {
  background-color: #00619490;
}


// Nav
.header {
  padding: 1rem 0;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.logo {
  text-align: center;
  font-size: var(--step-1);
}

.nav-link {
  text-decoration: none;
  color: #722F37;

  position: relative;
}
.nav-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  top: 35px;
  height: 2px;
  width: 100%;
  background-color: $clr-accent;
  
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 150ms ease-out;
}

.nav-link:hover::after,
.nav-link:focus::after {
  transform: scaleX(1);
  transform-origin: left;
}

.nav,
.nav-list {
  display: contents;
}

.nav-list>li:nth-of-type(-n + 3) {
  order: 1;
}

.logo {
  order: 2;
}


.nav-list>li:nth-of-type(n + 4) {
  order: 3;
}


// Hero
.hero {
  min-height: 80vh;
  isolation: isolate;

  position: relative;
  // margin-top: 0;
}


.hero-intro {
  padding-top: 4rem;
}

.hero-intro::after {
  content: "";
  position: absolute;
  padding: 5rem;
  // mix-blend-mode: color;

  inset: 0;
  z-index: -1;

  background-image: url(../../assets/images/vineyard1.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.back-img1 {
  background-image: url(../../assets/images/vineyard1.jpg);
}

.back-img2 {
  background-image: url(../../assets/images/vineyard2.jpg);
}

.back-img3 {
  background-image: url(../../assets/images/vineyard3.jpg);
}

.back-img4 {
  background-image: url(../../assets/images/vineyard4.jpg);
}

.img-logo {
  width: 90%;
  max-width: 600px;
}

// Media Scroller
.media-scroller {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: calc(100% - 2rem);

  overflow-x: auto;
  overscroll-behavior-inline: contain;
}

.media-element {
  display: grid;
  grid-template-rows: min-content;
  gap: 0.1rem;
  align-items: center;
  padding-bottom: 2rem;
}

.media-element>img {
  inline-size: 100%;
  height: 600px;
  object-fit: cover;
}


.media-scroller>div:first-of-type {
  filter: brightness(0.95);
}

.snaps-inline {
  scroll-snap-type: inline mandatory;
  // scroll-padding-inline: 1rem;
}

.snaps-inline>* {
  scroll-snap-align: start;
}

.media-first {
  position: relative;
  text-align: center;
  color: #fff;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wine-about__img {
  background-image: url(../../assets/images/winegrape2.jpg);
  height: 20rem;
}


// Products
.product__img-1 {
  background-image: url(../../assets/images/montepulciano1.jpg);
}

.product__img-2 {
  background-image: url(../../assets/images/sangiovese1.jpg);
}

.product__img-3 {
  background-image: url(../../assets/images/syrah1.jpg);
}

.product__img-4 {
  background-image: url(../../assets/images/trebbiano1.jpg);
}

.product__img {
  height: 200px;
  width: 400px;
  object-fit: cover;
}
// Form
.form-section {
  position: relative;
  width: 100%;
  max-height: fit-content;
  margin: 0;
  padding: 0 0 5rem;
  filter: brightness(0.95);
}

.form-section::after {
  content: "";
  position: absolute;
  background-image: url(../../assets/images/vineyard4.jpg);
  background-position: top middle;
  background-repeat: no-repeat;
  background-size: cover;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  filter: blur(4px);
  z-index: -1;
}

.form-container {
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.form-img {
  background-image: url(../../assets/images/vineyard4.jpg);
  min-height: 200px;
}
.form {
  padding: 1rem;
}


.form-input {
  width: 100%;
  margin-bottom: 50px;
}

textarea {
  resize: none;
}

.form-input,
.form-button {
  font-family: inherit;
  font-size: 14px;
  border: 0;
  // padding-block: 0.25rem;
}

.form-button {
  text-transform: uppercase;
  background-color: #00619465;
  border-radius: 3px;
  font-weight: 700;
  width: 100%;
}

.form-button:focus,
.form-button:hover {
  background-color: #00619490;
}

.form-input:hover,
.form-input:focus {
  background-color: rgba(0, 0, 0, 0.1);
}

.form-input::placeholder {
  opacity: 0.6;
}

.form-input:focus::placeholder,
.form-input:hover::placeholder {
  color: $clr-accent;
  opacity: 0.85;
}


// Footer
.site-footer {
  background-color: $clr-footer;
  color: #ffffff;
  margin-top: auto;
}

.footer-container {
  // padding-block: 1rem;
  padding-bottom: 10px;

  display: grid;
  row-gap: 2em;
  column-gap: 10px;
  grid-template-areas: 
    "logo logo . . ."
    "pages pages pages social social"
    "copyright copyright copyright copy-vat copy-vat";
}


.footer-logo {
  grid-area: logo;
}
.footer-logo img {
  max-width: 200px;
}

.footer-links {
  grid-area: pages;
}

.footer-link {
  color: #ffffff;
}

.footer-social {
  grid-area: social;
}
.social-link {
  color: #ffffff;
}
.social-link:hover {
  color: $clr-link;
}


.footer-copyright {
  grid-area: copyright;
}

.footer-vat {
  grid-area: copy-vat;
}

.footer-copyright p:first-child,
.footer-vat p:first-child {
  margin-bottom: 10px;
}

.footer-copyright > *,
.footer-vat > * {
  font-size: 12px;
}

.bi {
  font-size: var(--step-1);
}

@media screen and (max-width: 550px) {

  .footer-container {
    display: flex;
    flex-direction: column;
  }

  .mobile-only {
    display: block;
  }

  .media-element>img {
    height: 300px;
  }

}


